import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import Swal from 'sweetalert2';

declare const $: any;

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
  loginForm: FormGroup;
  submitted = false;
  returnUrl: string;
  // http: any;
  ress: any;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private http: HttpClient
  ) {}
  mainurl

  ngOnInit() {
    this.mainurl = localStorage.getItem('mainurl');
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.required],
      newpwd : ['', Validators.required],
      oldpwd : ['', Validators.required]
    });

    // get return url from route parameters or default to '/'
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

    //    [Focus input] * /
    $('.input100').each(function() {
      $(this).on('blur', function() {
        if (
          $(this)
            .val()
            .trim() != ''
        ) {
          $(this).addClass('has-val');
        } else {
          $(this).removeClass('has-val');
        }
      });
    });
  }
  get f() {
    return this.loginForm.controls;
  }
  showErrorAlert() {
    Swal.fire('Yikes!', 'username or password  went wrong!', 'error')
  }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.loginForm.invalid) {

      Swal.fire('Yikes!', 'username or password  wrong!', 'error')
      return;
    } else {
      console.log("login form values", this.loginForm.get('username').value)
      let resetData = {
        'username' : this.loginForm.get('username').value,
        'oldpwd' : this.loginForm.get('oldpwd').value,
        'newpwd' : this.loginForm.get('newpwd').value
      }
      console.log("data", resetData, typeof(resetData))
      this.http.post(this.mainurl + 'resetpwd', resetData).subscribe(
        res => {
          this.router.navigate(['/authentication/signin']);
          console.log(res);
          this.ress = res
          console.log('this is the respoce for password',this.ress.text);


          // tslint:disable-next-line: triple-equals
          if (this.ress.text == 'Password Reset Successful'){
            this.router.navigate(['/']);


            }


        },
        (err: HttpErrorResponse) => {
          console.log('this is the err.error',err.error);

          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
          if(err.status == 200){
            Swal.fire('Yikes!', 'username or password  sucess!', 'success')
            console.log('this is the err.status',err.status);
            this.router.navigate(['/authentication/signin']);


          }else{
            this.showErrorAlert();
          }
        }
      );


  }
}

}
