import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import Swal from 'sweetalert2';

declare const $: any;
@Component({
  selector: 'app-locked',
  templateUrl: './locked.component.html',
  styleUrls: ['./locked.component.scss']
})
export class LockedComponent implements OnInit {
  loginForm: FormGroup;
  submitted = false;
  returnUrl: string;
  hide = true;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router, private http: HttpClient
  ) {}
  url = 'http://30.118.179.12:2020/loginuid'
  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      password: ['', Validators.required]
    });

    // get return url from route parameters or default to '/'
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

    //    [Focus input] * /
    $('.input100').each(function() {
      $(this).on('blur', function() {
        if (
          $(this)
            .val()
            .trim() != ''
        ) {
          $(this).addClass('has-val');
        } else {
          $(this).removeClass('has-val');
        }
      });
    });
  }
  get f() {
    return this.loginForm.controls;
  }

  // onSubmit() {
  //   this.submitted = true;

  //   // stop here if form is invalid
  //   if (this.loginForm.invalid) {
  //     console.log('this.loginForm' ,this.loginForm.value);

  //     return;
  //   } else {
  //     this.router.navigate(['/dashboard/main']);
  //   }
  // }

  ress :any = {};

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.loginForm.invalid) {


      return;
    } else {



        this.http.post(this.url, this.loginForm.value).subscribe(
          res => {
            console.log(res);
            this.ress = res
            localStorage.setItem('username', this.ress.username);
            localStorage.setItem('userid', this.ress.id);
            localStorage.setItem('usertoken', this.ress.token);
            localStorage.setItem('userrole', this.ress.role);
            localStorage.setItem('mainurl', 'http://30.118.179.12:2020/');

            console.log('userrole', this.ress.role);
            // tslint:disable-next-line: triple-equals
            if (this.ress.role == 'Admin'){
              this.router.navigate(['/storemng/main']);


              }
              else if (this.ress.role == 'Supervisor'){
                this.router.navigate(['/empboard/othreq']);

              } else if (this.ress.role == 'Store Executive'){
                this.router.navigate(['/storemng/main']);

              }
              else if (this.ress.role == 'Store Manager'){
                this.router.navigate(['/storemng/main']);

              }
              else{
                this.router.navigate(['/empboard/othreq']);

              }
          },
          (err: HttpErrorResponse) => {
            console.log(err.error);
            this.showErrorAlert();
            console.log(err.name);
            console.log(err.message);
            console.log(err.status);
          }
        );


    }
  }
  showErrorAlert() {
    Swal.fire('Yikes!', 'uid  went wrong!', 'error')
  }
}
